<template>
  <div class="new_msg">
    <navigation-bar
      pageName="消息"
      @onLeftClick="goBack"></navigation-bar>

    <div class="new_msg_list">
        <van-list
                v-model="loading"
                :finished="finished"
                :immediate-check="false"
                @load="loadMore"
                :finished-text="newMsgList.legnth > 0 ? '无更多数据...' : ''" >
            <div class="item" v-for="(item, index) in newMsgList" :key="index" @click="goDetail(item)">
                <div class="head">
                    <img v-if="item.headPic" :src="item.headPic" alt="">
                    <img v-else src="../../assets/imgs/find/avatar.png" alt />
                </div>
                <div class="middle">
                    <div class="name" v-if="item.nickName">{{item.nickName}}</div>
                    <div class="content">
                        <img v-if="!item.type" src="../../assets/imgs/find/praise_icon.png" />
                        {{item.evalMessage}}
                    </div>
                    <div class="time">{{item.createdTime}}</div>
                </div>
                <div class="note-con">
                    <img
                      v-if="item.notePic"
                      class="article-img"
                      :src="item.notePic"
                      v-lazy="item.notePic" :key="item.notePic"
                      alt
                    />
                    {{item.noteView}}
                </div>
            </div>
        </van-list>
    </div>
  </div>
</template>

<script>
import { getNewMsgListService } from "../../services/newFindService";
import { store } from "../../utils/store";
import Log from "../../utils/log";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "newMsg",
  components: { NavigationBar },
  mounted() {
  },
  activated() {
    if (this.$route.params.fromFind) {
        this.userInfo = store.get("userInfo");
        this.resetMsgData();
        this.getNewMsgList();
    }
  },
  data() {
    return {
      newMsgList: [],
      loading: false,
      finished: false,
      requestType: 0, // 请求参数，0请求未读的，1请求全部的。
      page: 1,
      rows: 10,
    };
  },
  methods: {
    /**
     * 重置消息列表参数
     **/
    resetMsgData() {
        this.newMsgList = [];
        this.loading = false;
        this.finished = false;
        this.requestType = 0; // 请求参数，0请求未读的，1请求全部的。
        this.page = 1;
        this.rows = 10;
    },

    /**
     * 获取消息列表
     **/
    getNewMsgList() {
      getNewMsgListService({
          ownerCode: this.userInfo.USER_CODE,
          areaCode: this.userInfo.AREA_CODE,
          page: this.page,
          rows: this.rows,
          status: 0
      }).then(
          result => {
              this.loading = false;
              if (result.resultCode == "0") {
                  this.finished = result.data.list.length < 10;
                  if (this.page == 1) {
                      this.newMsgList = result.data.list;
                  } else {
                      this.newMsgList = this.newMsgList.concat(result.data.list);
                  }
              } else {
                  this.$toast(result.msg);
                  this.finished = true;
              }
              Log.i(this.newMsgList);
          },
          error => {
              this.finished = true;
              this.loading = false;
              this.$toast(error);
          }
      ).catch(error => {
          this.$toast(error)
      })
    },

    /**
     * 跳转到消息详情
     **/
    goDetail(item) {
      if (item.noteStatus == 1) {
        this.$toast("帖子已删除");
      } else {
        this.$router.push({
          name: "newMsgDetail",
          params: {
            noteCode: item.noteCode,
            refreshDetail: 1
          }
        });
      }
    },

    loadMore() {
    },

    goBack() {
      this.$router.back(-1);
    }
  }
};
</script>

